  font-size: var(--au-font-size-base);
  cursor: pointer;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns='http://www.w3.org/2000/svg'%20width='8'%20height='5'%20viewBox='0%200%208%205'%3E%3Cpath%20fill='%23ffffff'%20d='M0%200l4%205l4-5z'/%3E%3C/svg%3E");
  background-position: right 2px center;
  background-repeat: no-repeat;
  background-size: 8px 5px;
  padding: 8px 28px 8px 8px;
  transition: background .2s ease-in-out;
}

select > option {
  background-color: var(--au-accent-sub-tint);
