.checkbox {
  cursor: pointer;
  user-select: none;
  align-items: center;
  gap: 8px;
  display: flex;
}

.checkbox input {
  display: none;
}

.checkbox .checkmark {
  border: 1px solid var(--au-border-default);
  background-color: var(--au-bg-sub);
  width: 10px;
  height: 10px;
  transition: border .2s, background-color .2s;
  display: inline-block;
  position: relative;
}

.checkbox input:checked + .checkmark {
  border: 1px solid var(--au-border-default);
  background-color: var(--au-bg-sub);
}

.checkbox input:checked + .checkmark:after {
  content: "";
  border: solid var(--au-accent-default);
  box-sizing: border-box;
  border-width: 0 1px 1px 0;
  width: 4px;
  height: 8px;
  position: absolute;
  top: 1px;
  left: 3px;
  transform: rotate(45deg);
}

.range {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

.range-label {
  font-family: var(--au-font-base);
