.range-control {
  appearance: none;
  cursor: pointer;
  background: none;
  border: none;
  width: 100%;
  height: 8px;
  padding: 5px 0;
}

.range-control::-webkit-slider-runnable-track {
  border: 1px solid var(--au-border-sub);
  height: 8px;
}

.range-control::-webkit-slider-thumb {
  appearance: none;
  background: var(--au-accent-default);
  border: none;
  width: 14px;
  height: 14px;
  margin-top: -3px;
}

.range-control::-moz-range-track {
  border: 1px solid var(--au-border-sub);
  height: 8px;
}

.range-control::-moz-range-progress {
  background-color: var(--au-accent-sub);
  height: 8px;
}

.range-control::-moz-range-thumb {
  background: var(--au-accent-default);
  border: none;
  width: 14px;
  height: 14px;
}

.range-control::-ms-track {
  color: #0000;
  background: none;
  border-color: #0000;
  height: 8px;
}

.range-control::-ms-fill-lower {
  background: var(--au-accent-default);
}

.range-control::-ms-thumb {
  background: var(--au-accent-default);
  border: none;
  width: 14px;
  height: 14px;
}

.range-value {
